export const isNone = (s) => s === null;
export const isSome = (s) => s != null;
export function None(_) {
    return null;
}
export function Some(t) {
    return t;
}
export const isOk = (t) => t.ok !== undefined;
export const isErr = (e) => e.err !== undefined;
export const isResult = (t) => isOk(t) || isErr(t);
export function Ok(t) {
    return t.ok
        ? t.ok
        : {
            ok: t,
            map(fn) {
                return fn(Ok(this));
            },
        };
}
export function Err(e) {
    return (e.err ?? {
        err: e,
        map(_fn) {
            return this;
        },
    });
}
export function unwrap(t) {
    if (isNone(t)) {
        throw new Error(`Attempted to unwrap None`);
    }
    if (isErr(t)) {
        throw Err(t);
    }
    if (isOk(t)) {
        return Ok(t);
    }
    return t;
}
export function unwrapOr(t, def) {
    if (isNone(t)) {
        return def;
    }
    if (isErr(t)) {
        return def;
    }
    if (isOk(t)) {
        return Ok(t);
    }
    return t;
}
export function unwrapOrElse(t, def) {
    if (isNone(t)) {
        return def();
    }
    if (isErr(t)) {
        return def();
    }
    if (isOk(t)) {
        return Ok(t);
    }
    return t;
}
